* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	appearance: none;
}
$black: #000000;
$darkgrey: #303030;
$white: #ffffff;


@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap');

@font-face {
	font-family: 'Icon';
	src: 
		local('fonts/icon'), url('../media/fonts/icon/icon.ttf'),
		local('fonts/icon'), url('../media/fonts/icon/icon.woff2');
	font-display: block;
}
* {
	color: $darkgrey;
}
* {
	font-family: 'Inter', sans-serif;
	font-size: 18px;
	line-height: 1.5
}

.icon {
  display: inline-block;
  font: normal normal normal 1em/1 'Icon';
  color: inherit;
  flex-shrink: 0;
  speak: none;
  text-transform: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

$icons: (
  arrow: 'ea02',
  moon: 'ea03',
  sun: 'ea04',
  reload: 'ea05',
);

@function unicode($str) {
  @return unquote("\"\\#{$str}\"");
}

@each $name, $code in $icons {
  .icon--#{"" +$name}::before {
    content: unicode($code);
  }
}
@keyframes timePointsPulse {
  0% { opacity: 1 }
  50% { opacity: 0 }
  100%{ opacity: 1 }
}
.clock {
  
  &__inner {
    display: grid;
    height: 100vh;
    grid-template-areas: 
      "quote ."
      "greeting . "
      "time toggle"
      "location toggle";
    grid-template-columns: 1fr 160px;
    grid-template-rows: 1fr auto auto auto;
    justify-content: start;
    align-items: start;
    padding: 56px 160px;
    transition: all 1s ease;
  }

  &__quote, .quote {
    grid-area: quote;
    max-width: 400px;
    
    &__text {
      color: $white;
      margin-bottom: 12px;

      &::before { content: "\""; }
      &::after { content: "\""; }
    }

    &__author, .author {
      color: $white;
      font-weight: 700;

    }

  }

  &__greeting, .greeting {
    grid-area: greeting;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    font-size: 20px;
    text-transform: uppercase;
    letter-spacing: 4px;

    &__icon {
      color: $white;
      margin-right: 16px;
    }

    &__text {
      color: $white;
    }

  }

  &__time, .time {
    grid-area: time;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-end;
    
    
    &__time, .time {
      margin-right: 32px;

      &__hour, 
      &__points,
      &__minute {        
        color: $white;
        font-size: 200px;
        font-weight: 700;
        line-height: 1;
      }

      &__points  {
        animation-duration: 1s;
        animation-name: timePointsPulse;
        animation-iteration-count: infinite;
        animation-timing-function: ease;
      }

    }

    &__timezone {
      color: $white;
      margin-bottom: 12px;
      font-size: 40px;
    }

  }

  &__location, .location {
    grid-area: location;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;


    &__city {
      color: $white;
      text-transform: uppercase;
      margin-right: 16px;
      font-size: 24px;
      font-weight: 700;
      letter-spacing: 4px;
      
      &::after {
        content: ",";
        color: $white;
      }
    
    }

    &__country {
      color: $white;
      text-transform: uppercase;
      font-size: 24px;
      font-weight: 700;
      letter-spacing: 4px;
    }

  }

  &__toggle, .toggle {
    background-color: $white;
    grid-area: toggle;
    width: 160px;
    height: 56px;
    border-radius: 56px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 8px 8px 8px 24px;
    cursor: pointer;
    align-self: end;

    &__input, .input {
      width: 40px;
      height: 40px;
      background-color: $darkgrey;
      border-radius: 50%;
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-content: center;

      &__icon {
        color: $white;
        font-size: 24px;
        align-self: center;
        transform: rotate(180deg);
        transition: all 0.5s ease;
      }

    }

    &__label {
      line-height: 1;
      font-size: 18px; 
      margin-top: 4px;
      font-weight: 700;
      letter-spacing: 4px;
      color: $black;
      opacity: 0.5;
      text-transform: uppercase;
    }

    &--inactive {
      
      & .input__icon {
        transform: rotate(0deg);
      }

    }

  }

}
.page {
	width: 100vw;
	min-height: 100vh;
	background-size: cover;
	background-position: center;
	position: relative;
	opacity: 0;
	transition: all 2s ease;
	overflow: hidden;

	&__overlay {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background-color: $white;
		pointer-events: none;
		opacity: 1;
		transition: all 2s ease;
		display: none;
	}

	&--day {
		background-image: url('/media/images/background-day.webp');
	}

	&--night {
		background-image: url('/media/images/background-night.webp');
	}

	&--day, 
	&--night {
		opacity: 1;

		& .page__overlay {
			opacity: 0;
		}

	}
}
.panel {
  background: rgba(255, 255, 255, 0.75);
  backdrop-filter: blur(4.7742px); 

  &__inner {
    padding: 56px 160px;
    display: grid;
    grid-template-areas: 
      "timezone day-of-the-week"
      "day-of-the-year week-number";
  }

  & .label {
    font-size: 16px;
    text-transform: uppercase;
    font-weight: 400;
    letter-spacing: 2px;;
  }

  & .value {
    font-size: 56px;
    font-weight: 700;
  }

  &__timezone {
    grid-area: timezone;
    padding-bottom: 32px;
  }

  &__day-of-week {
    grid-area: day-of-the-week;
    border-left: 1px solid $darkgrey;
    padding-bottom: 32px;
    padding-left: 64px;
  }

  &__day-of-year {
    grid-area: day-of-the-year;
  }

  &__week-number {
    grid-area: week-number;
    border-left: 1px solid $darkgrey;
    padding-left: 64px;
  }



}